"use client";

import * as React from "react";
import {
  Plus,
  Search,
  MoreVertical,
  Eye,
  Trash2,
  Edit,
  ExternalLink,
  BarChart3,
  Layout,
  X,
  Link2,
  CheckCircle2,
  Palette,
  Check,
  Paintbrush,
  Loader2,
} from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuTrigger,
  DropdownMenuContent,
  DropdownMenuItem,
} from "@/components/ui/dropdown-menu";
import { useRouter } from "next/navigation";
import axiosClient from "@/lib/axiosClient";
import { DashboardLayout } from "@/components/dashboard/layout";

// Mock UI Components (unchanged)
const Button = ({ children, className = "", variant = "default", size = "default", onClick, disabled, ...props }: any) => {
  const baseStyles = "inline-flex items-center justify-center rounded-md font-medium transition-colors focus-visible:outline-none disabled:pointer-events-none disabled:opacity-50";
  const variants = {
    default: "bg-blue-600 text-white hover:bg-blue-700",
    outline: "border border-gray-300 bg-white hover:bg-gray-50",
    ghost: "hover:bg-gray-100",
  };
  const sizes = {
    default: "h-10 px-4 py-2",
    sm: "h-9 px-3 text-sm",
    icon: "h-10 w-10",
  };
  return (
    <button
      className={`${baseStyles} ${variants[variant]} ${sizes[size]} ${className}`}
      onClick={onClick}
      disabled={disabled}
      {...props}
    >
      {children}
    </button>
  );
};

const Input = ({ className = "", ...props }: any) => (
  <input
    className={`flex h-10 w-full rounded-md border border-gray-300 bg-white px-3 py-2 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500 ${className}`}
    {...props}
  />
);

const Card = ({ children, className = "" }: any) => (
  <div className={`rounded-lg border bg-white shadow-sm ${className}`}>{children}</div>
);

const CardHeader = ({ children, className = "" }: any) => (
  <div className={`flex flex-col space-y-1.5 p-6 ${className}`}>{children}</div>
);

const CardTitle = ({ children }: any) => <h3 className="text-xl font-semibold">{children}</h3>;

const CardContent = ({ children, className = "" }: any) => (
  <div className={`p-6 pt-0 ${className}`}>{children}</div>
);

const Badge = ({ children, className = "", variant = "default" }: any) => {
  return (
    <span className={`inline-flex items-center rounded-full px-2.5 py-0.5 text-xs font-semibold ${className}`}>
      {children}
    </span>
  );
};

type LandingPage = {
  id: number;
  userId: number | null;
  name: string;
  url: string;
  views: number;
  conversions: number;
  status: string;
  lastModified: string;
  template: string;
  isActive: boolean;
};

export default function LandingPagesPage() {
  const [loading, setLoading] = React.useState(true);
  const [pages, setPages] = React.useState<LandingPage[]>([]);
  const [activePage, setActivePage] = React.useState<LandingPage | null>(null);
  const [showLinkModal, setShowLinkModal] = React.useState(false);
  const [generatedLink, setGeneratedLink] = React.useState("");
  const [selectedPage, setSelectedPage] = React.useState<LandingPage | null>(null);
  const [copied, setCopied] = React.useState(false);
  const router = useRouter();

  // TODO: Replace with real auth user ID
  const currentUserId = 1;

  // Fetch all landing pages
  React.useEffect(() => {
    const fetchPages = async () => {
      try {
        setLoading(true);
        // Replace with your actual API endpoint
        const response = await axiosClient.get("/manageapi/templatelist"); // adjust endpoint
        let data = response.data;
   
        // If your API wraps in { pages: [...] }, adjust accordingly
        if (!Array.isArray(data)) {
          data = data.pages || data.data || [];
        }

        // Simulating your provided JSON response
       

        // Use mockData or real response
        const fetchedPages = data;

        setPages(fetchedPages);

        // Find active page (only one should be true)
        const active = fetchedPages.find(p => p.isActive) || null;
        setActivePage(active);

      } catch (error) {
        console.error("Failed to fetch landing pages:", error);
      } finally {
        setLoading(false);
      }
    };

    fetchPages();
  }, []);

  // Pages visible in template grid: default (userId null) + user's own (userId matches) + not active
  const availablePages = pages.filter(page => {
    const isDefault = page.userId === null;
    const isUserOwned = page.userId === currentUserId;
    const isNotActive = !page.isActive;
    return (isDefault || isUserOwned) && isNotActive;
  });

  const generateDynamicURL = (page: LandingPage) => {
    return `https://yourdomain.com/l/${page.id}`; // or use page.template or slug
  };

  const handleGenerateLink = (page: LandingPage) => {
    setSelectedPage(page);
    const link = generateDynamicURL(page);
    setGeneratedLink(link);
    setShowLinkModal(true);
    setCopied(false);
  };

  const handleCopyLink = () => {
    navigator.clipboard.writeText(generatedLink);
    setCopied(true);
    setTimeout(() => setCopied(false), 2000);
  };

  const handleOpenInNewTab = () => {
    window.open(generatedLink, "_blank");
  };

  const handleActivatePage = async (page: LandingPage) => {
    try {
      // Optimistic UI update
      const updatedPages = pages.map(p => ({
        ...p,
        isActive: p.id === page.id
      }));
      setPages(updatedPages);
      setActivePage(page);

      // TODO: Call API to update isActive = true for this page, false for others
      // await axiosClient.patch(`/api/landing-pages/${page.id}/activate`);

    } catch (error) {
      console.error("Failed to activate page", error);
      // Revert on error if needed
    }
  };

  const handleDeactivateActivePage = async () => {
    if (!activePage) return;

    try {
      const updatedPages = pages.map(p => ({
        ...p,
        isActive: false
      }));
      setPages(updatedPages);
      setActivePage(null);

      // TODO: API call to deactivate
      // await axiosClient.patch(`/api/landing-pages/${activePage.id}/deactivate`);
    } catch (error) {
      console.error("Failed to deactivate", error);
    }
  };

  const handleEditPage = (page: LandingPage) => {
    router.push(`/landing-pages/edit/${page.id}`);
  };

  if (loading) {
    return (
      <DashboardLayout>
        <div className="min-h-screen bg-gray-50 flex items-center justify-center">
          <div className="text-center">
            <Loader2 className="h-8 w-8 animate-spin mx-auto mb-4" />
            <p>Loading your landing pages...</p>
          </div>
        </div>
      </DashboardLayout>
    );
  }

  return (
    <DashboardLayout>
      <div className="min-h-screen bg-gray-50">
        <div className="bg-white border-b">
          <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-6">
            <div className="flex flex-col gap-4 sm:flex-row sm:items-center sm:justify-between">
              <div>
                <h1 className="text-3xl font-bold text-gray-900">Landing Pages</h1>
                <p className="text-sm text-gray-500 mt-1">Manage and publish your product landing pages</p>
              </div>
            </div>
          </div>
        </div>

        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">

          {/* Active Page Section */}
          {activePage && (
            <div className="mb-12">
              <div className="flex items-center justify-between mb-4">
                <div className="flex items-center gap-3">
                  <h2 className="text-2xl font-bold text-gray-900">Active Landing Page</h2>
                  <Badge className="bg-green-100 text-green-800">
                    <Check className="h-3 w-3 mr-1" />
                    Live
                  </Badge>
                </div>
              </div>

              <Card className="overflow-hidden border-2 border-green-500 shadow-lg">
                <div className="md:flex">
                  <div className="bg-gray-100 p-8">
                    <div className="aspect-[4/3] w-80 relative rounded-lg overflow-hidden bg-white shadow-xl">
                      <img src={activePage.url} alt={activePage.name} className="w-full h-full object-cover" />
                    </div>
                  </div>

                  <div className="p-8 flex-1">
                    <h3 className="text-2xl font-bold mb-4">{activePage.name}</h3>

                    <div className="grid grid-cols-3 gap-6 mb-8">
                      <div className="bg-blue-50 p-5 rounded-xl text-center">
                        <div className="text-3xl font-bold text-blue-600">{activePage.views.toLocaleString()}</div>
                        <div className="text-sm text-gray-600 mt-1">Total Views</div>
                      </div>
                      <div className="bg-green-50 p-5 rounded-xl text-center">
                        <div className="text-3xl font-bold text-green-600">{activePage.conversions}</div>
                        <div className="text-sm text-gray-600 mt-1">Conversions</div>
                      </div>
                      <div className="bg-purple-50 p-5 rounded-xl text-center">
                        <div className="text-3xl font-bold text-purple-600">
                          {activePage.views > 0 ? ((activePage.conversions / activePage.views) * 100).toFixed(1) : "0"}%
                        </div>
                        <div className="text-sm text-gray-600 mt-1">Conversion Rate</div>
                      </div>
                    </div>

                    <div className="flex flex-wrap gap-4">
                      <Button onClick={() => handleEditPage(activePage)}>
                        <Edit className="h-4 w-4 mr-2" /> Edit Design
                      </Button>
                      <Button variant="outline" onClick={() => handleGenerateLink(activePage)}>
                        <Link2 className="h-4 w-4 mr-2" /> Share Link
                      </Button>
                      <Button variant="outline" className="text-red-600 border-red-300" onClick={handleDeactivateActivePage}>
                        <X className="h-4 w-4 mr-2" /> Deactivate
                      </Button>
                    </div>
                  </div>
                </div>
              </Card>
            </div>
          )}

          {/* Available Templates */}
          <div>
            <h2 className="text-xl font-bold mb-6">
              {activePage ? "Switch to Another Template" : "Choose a Template to Activate"}
            </h2>

            {availableTemplates.length > 0 ? (
  <div className="grid grid-cols-2 md:grid-cols-3 lg:grid-cols-5 gap-4">
    {availableTemplates.map((template) => (
      <Card
        key={template.key}
        className="overflow-hidden hover:shadow-xl transition-all cursor-pointer group"
      >
        {/* Thumbnail + Preview Button */}
        <div className="relative bg-gray-100 flex items-center justify-center py-8">
          <img
            src={template.image}
            alt={template.name}
            className="w-[70%] h-auto object-contain"
          />
          <div className="absolute inset-0 bg-black/0 group-hover:bg-black/40 transition-all flex items-center justify-center">
            <Button
              className="opacity-0 group-hover:opacity-100 transition-opacity"
              onClick={() =>
                window.open(
                  `http://localhost:3000/preview/demo?template=${template.key}`,
                  "_blank"
                )
              }
            >
              <Eye className="h-4 w-4 mr-2" />
              Preview
            </Button>
          </div>
        </div>

        <CardContent className="p-4">
          <div className="flex items-center justify-between mb-3 mt-3">
            <h4 className="font-semibold text-sm">{template.name}</h4>
            <Badge className={`${template.color} text-xs`}>
              {template.icon}
            </Badge>
          </div>

          {/* Responsive Action Buttons */}
          <div className="flex gap-2 items-center">
            {/* Desktop/Tablet: Two buttons */}
            <div className="hidden sm:flex gap-2 w-full">
              <Button
                variant="outline"
                size="sm"
                className="flex-1 text-xs"
                onClick={() => handleCustomizeTemplate(template.key)}
              >
                <Paintbrush className="h-3.5 w-3.5 mr-1" />
                Customize
              </Button>
              <Button
                size="sm"
                className="flex-1 text-xs"
                onClick={() => handleActivateTemplate(template.key)}
              >
                <Check className="h-3.5 w-3.5 mr-1" />
                Activate
              </Button>
            </div>

            {/* Mobile: Three-dot menu */}
            <div className="flex sm:hidden w-full justify-end">
              <DropdownMenu>
                <DropdownMenuTrigger asChild>
                  <Button variant="ghost" size="sm" className="px-2">
                    <MoreVertical className="h-5 w-5" />
                  </Button>
                </DropdownMenuTrigger>
                <DropdownMenuContent align="end">
                  <DropdownMenuItem
                    onClick={() =>
                      window.open(
                        `http://localhost:3000/preview/demo?template=${template.key}`,
                        "_blank"
                      )
                    }
                  >
                    <Eye className="h-4 w-4 mr-2" />
                    Preview
                  </DropdownMenuItem>
                  <DropdownMenuItem
                    onClick={() => handleCustomizeTemplate(template.key)}
                  >
                    <Paintbrush className="h-4 w-4 mr-2" />
                    Customize
                  </DropdownMenuItem>
                  <DropdownMenuItem
                    onClick={() => handleActivateTemplate(template.key)}
                  >
                    <Check className="h-4 w-4 mr-2" />
                    Activate
                  </DropdownMenuItem>
                </DropdownMenuContent>
              </DropdownMenu>
            </div>
          </div>
        </CardContent>
      </Card>
    ))}
  </div>
) : (
  <Card className="p-8 text-center">
    <p className="text-gray-500">All templates are currently in use or activated</p>
  </Card>
)}
          </div>

          {/* Link Share Modal */}
          {showLinkModal && selectedPage && (
            <div className="fixed inset-0 z-50 bg-black/70 flex items-center justify-center p-4">
              <div className="bg-white rounded-xl shadow-2xl max-w-lg w-full overflow-hidden">
                <div className="bg-green-50 p-6 border-b border-green-200">
                  <div className="flex items-center justify-between">
                    <div className="flex items-center gap-4">
                      <CheckCircle2 className="h-12 w-12 text-green-600" />
                      <div>
                        <h3 className="text-xl font-bold">Shareable Link Ready!</h3>
                        <p className="text-sm text-gray-600">{selectedPage.name}</p>
                      </div>
                    </div>
                    <Button variant="ghost" size="icon" onClick={() => setShowLinkModal(false)}>
                      <X className="h-5 w-5" />
                    </Button>
                  </div>
                </div>

                <div className="p-6 space-y-5">
                  <div className="flex gap-3">
                    <Input value={generatedLink} readOnly className="font-mono text-sm" />
                    <Button onClick={handleCopyLink}>
                      {copied ? "Copied!" : "Copy"}
                    </Button>
                  </div>
                  <Button className="w-full" onClick={handleOpenInNewTab}>
                    <ExternalLink className="h-4 w-4 mr-2" />
                    Open in New Tab
                  </Button>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </DashboardLayout>
  );
}